@tailwind base;
@tailwind components;
@tailwind utilities;

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #222;
  border-radius: 20px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 251, 235, 0.8);
  border-radius: 10px;
}

#tailwindtext {
  background: -webkit-linear-gradient(rgba(129, 140, 248), rgba(147, 197, 253));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
#nodetext {
  color: #8cc84b;
}
